import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Table, Button, Input, Typography } from 'antd';
import Popup from './Popup';
const { Search } = Input;
const { Text } = Typography;

function NewComp() {
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState([]);
    const [columns, setColumns] = useState([]);
    const [sponsoredArray, setSponsoredArray] = useState([]);
    const [searchText, setSearchText] = useState("");

    useEffect(() => {
        const fetchData = async () => {
            setLoading(true);
            try {
                const response = await axios.get('/api');
                const { columnNames, rowData, sponsored } = response.data;
                setSponsoredArray(sponsored);
                const apiColumns = columnNames.map((columnName, index) => ({
                    title: columnName,
                    dataIndex: `col${index}`, // Create unique dataIndex for each column
                    key: `col${index}`,
                    responsive: ['sm'],
                }));
                apiColumns[0] = {
                    title: 'Company Name',
                    dataIndex: 'col13',
                    key: 'col13',
                    render: (text, record) => (
                        <a href={record.col13} target="_blank" rel="noopener noreferrer">
                            {/* <Text
                                style={{
                                    width: 60
                                }}
                                ellipsis={{
                                    tooltip: record.col0
                                }}
                            >
                                {record.col0}
                            </Text > */}
                            {record.col0}
                        </a>
                    ),
                };
                apiColumns[4] = {
                    title: "Qualifications",
                    dataIndex: 'col4',
                    key: 'col4',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col4
                            }}
                        >
                            {record.col4}
                        </Text >
                    ),
                };
                apiColumns[7] = {
                    title: "Located",
                    dataIndex: 'col7',
                    key: 'col7',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col7
                            }}
                        >
                            {record.col7}
                        </Text >
                    ),
                };
                apiColumns[9] = {
                    title: "Information",
                    dataIndex: 'col9',
                    key: 'col9',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col9
                            }}
                        >
                            {record.col9}
                        </Text >
                    ),
                };
                apiColumns[10] = {
                    title: "Further Info",
                    dataIndex: 'col10',
                    key: 'col10',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col10
                            }}
                        >
                            {record.col10}
                        </Text >
                    ),
                };
                apiColumns[11] = {
                    title: "Positive Thoughts",
                    dataIndex: 'col11',
                    key: 'col11',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col11
                            }}
                        >
                            {record.col11}
                        </Text >
                    ),
                };
                apiColumns[12] = {
                    title: "Negative Thoughts",
                    dataIndex: 'col12',
                    key: 'col12',
                    render: (text, record) => (
                        <Text
                            style={{
                                width: 60
                            }}
                            ellipsis={{
                                tooltip: record.col12
                            }}
                        >
                            {record.col12}
                        </Text >
                    ),
                };

                // Add a new column for the "Profile" button
                apiColumns.push({
                    title: 'Profile',
                    dataIndex: 'profile',
                    key: 'profile',
                    render: (text, record) => (
                        <a href={record.col13} target="_blank" rel="noopener noreferrer" style={{ color: "white", backgroundColor: "black", padding: "6px", borderRadius: "10px" }}>
                            Profile
                        </a>
                    ),
                });
                // Add a new column for the Vote buttons
                apiColumns.push({
                    title: 'Vote',
                    dataIndex: 'vote',
                    key: 'vote',
                    render: (text, record, index) => (
                        <div>
                            <Button style={{ padding: "4px", fontSize: "10px" }} onClick={() => handleVote(record, 'up', index)}>Vote Up</Button> <br />
                            <Button style={{ padding: "4px", fontSize: "10px", marginTop: "4px" }} onClick={() => handleVote(record, 'down', index)}>Vote Down</Button>
                            {/* {record['vote']} */}
                        </div>
                    ),
                });
                setColumns(apiColumns);
                setData(
                    rowData.map((row, rowIndex) =>
                        row.reduce((acc, value, index) => {
                            acc[`col${index}`] = value;
                            acc[`id`] = rowIndex;
                            return acc;
                        }, { vote: 0 }) // Initialize the 'vote' property with 0
                    )
                );
                setLoading(false);
            } catch (error) {
                console.error('Error fetching data: ', error);
                setLoading(false);
            }
        };
        fetchData();
    }, []);
    // console.log(columns);
    const handleVote = (record, type, indexOfObj) => {
        // console.log(record, type, indexOfObj);
        const getStorage = JSON.parse(localStorage.getItem(record?.id))
        if (getStorage && getStorage?.record?.id === record?.id) {
            console.log("CALLED")
        } else {
            setData((prevData) =>
                prevData.map((row, index) => {
                    console.log(row)
                    if (row.id === record.id) {
                        const updatedRow = { ...row }; // Create a copy of the row
                        if (type === 'up') {
                            updatedRow.col14 = parseInt(row.col14) + 1; // Update col14 for upvote
                        } else if (type === 'down') {
                            updatedRow.col14 = parseInt(row.col14) - 1; // Update col14 for downvote
                        }
                        return updatedRow;
                    }
                    return row;
                })
            );
            localStorage.setItem(record?.id, JSON.stringify({ record, type, indexOfObj }));
        }
    };
    const onSearch = (value) => {
        setSearchText(value);
    }

    return (
        loading ?
            <div>Loading...</div>
            :
            <div>
                <div className="sponsored">
                    {
                        sponsoredArray.map((item, index) => {
                            return (
                                item[1]?.includes("https") &&
                                <>
                                    <div className='box' key={index}>
                                        <div>
                                            <img src={item[1]} alt="Sponsor" />
                                        </div>
                                        <div>
                                            <a href={item[3]} target="_blank" rel="noreferrer">{item[2]}</a>
                                        </div>
                                    </div>
                                </>
                            )
                        })
                    }
                </div>

                <header>
                    <div className='search'>
                        <Search
                            allowClear
                            placeholder="Search by company name"
                            onSearch={onSearch}
                            style={{
                                width: "100%",
                                maxWidth: "600px",
                            }}
                        />
                    </div>
                    <div className='popup'>
                        <Popup buttonText="Add a company" popupText={"<div>To add a new company that you own or work for please email as much information as you have on that company to Founder@TeachersXchange.com with the Subject: Add a Company</div>"} />
                    </div>
                    <div className='popup'>
                        <Popup buttonText="Suggest changes" popupText={"<div>To fix any wrong information, add additional information, add a new company etc. please click on the ‘’Profile’’ button for the company you want changes made to then comment your changes on that companies profile page</div>"} />
                    </div>
                </header>
                <div className='tableCon'>
                    <Table
                        dataSource={data?.filter(f => f?.col0?.toLowerCase().includes(searchText?.toLowerCase())).map(item => ({ ...item }))}
                        columns={columns.map((column) => {
                            const dataIndex = column.dataIndex;
                            return {
                                ...column,
                                sorter: (a, b) => {
                                    if (column === 'col14') {
                                        // Sort the "Vote" column numerically in descending order by default
                                        return b[`col${dataIndex}`] - a[`col${dataIndex}`];
                                    }
                                    // Implement custom sorting logic based on the column's dataIndex
                                    const isNumeric = !isNaN(Number(a[dataIndex])) && !isNaN(Number(b[dataIndex]));
                                    if (isNumeric) {
                                        // Sort numbers numerically
                                        return a[dataIndex] - b[dataIndex];
                                    } else {
                                        // Sort text alphabetically
                                        return a[dataIndex].localeCompare(b[dataIndex]);
                                    }
                                },
                            };
                        })}
                        scroll={{ x: 'max-content' }}
                    />
                </div>
            </div>
    );
}

export default NewComp;
